* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 530;
  font-style: bold;
  justify-content: space-between;
}

:root {
  --blue-color: #00acdc;
}

header {
  text-align: center;
  background-color: #fff;
  justify-content: space-between;
  width: 100%;
  height: 100px;
  padding: 0 20px;
  display: flex;
}

nav ul {
  align-items: center;
  margin-top: 10px;
  margin-right: 30px;
  list-style-type: none;
  display: flex;
}

nav ul li + li {
  margin-left: 20px;
}

.nav-link, nav ul li a {
  text-transform: uppercase;
  color: #000;
  line-height: 40px;
  text-decoration: none;
  position: relative;
}

nav a.is-active {
  color: var(--blue-color);
  position: relative;
}

nav a.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 5px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: -20px;
  left: 0;
}

nav a:hover {
  color: var(--blue-color);
}

.logo img {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-left: 30px;
}
